@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap');

.login-page {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background: radial-gradient(circle at center, #1f2937 0%, #0f172a 100%);
  font-family: 'Outfit', sans-serif;
  overflow: hidden;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    width: 150%;
    height: 150%;
    background: repeating-linear-gradient(
      0deg,
      transparent 0,
      transparent 1px,
      rgba(0, 255, 200, 0.03) 1px,
      rgba(0, 255, 200, 0.03) 2px
    );
    pointer-events: none;
    animation: scan 10s linear infinite;
  }
}

@keyframes scan {
  from { transform: translateY(0); }
  to { transform: translateY(-50px); }
}

.login-card {
  width: 100%;
  max-width: 400px;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  padding: 40px;
  z-index: 1;

  .ant-typography {
    color: #fff;
    text-align: center;
    margin-bottom: 30px;
    
    &.title {
      font-weight: 600;
      letter-spacing: 1px;
    }
    
    &.subtitle {
      color: rgba(255, 255, 255, 0.6);
      font-weight: 300;
      font-size: 14px;
    }
  }

  .ant-form-item-label > label {
    color: rgba(255, 255, 255, 0.8) !important;
  }

  .ant-input {
    background: rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.1);
    color: #fff;
    padding: 10px 15px;
    border-radius: 8px;
    
    &::placeholder {
      color: rgba(255, 255, 255, 0.3);
    }

    &:hover, &:focus {
      border-color: #00b96b;
      background: rgba(0, 0, 0, 0.3);
      box-shadow: none;
    }
  }

  .ant-input-password {
    background: rgba(0, 0, 0, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    padding: 10px 15px;

    input {
      background: transparent;
      color: #fff;
      
      &::placeholder {
        color: rgba(255, 255, 255, 0.3);
      }
    }

    .ant-input-suffix {
      color: rgba(255, 255, 255, 0.5);
    }

    &:hover, &:focus-within {
      border-color: #00b96b;
      background: rgba(0, 0, 0, 0.3);
    }
  }

  .login-button {
    width: 100%;
    height: 45px;
    background: linear-gradient(90deg, #00b96b 0%, #009456 100%);
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    margin-top: 10px;
    
    &:hover {
      background: linear-gradient(90deg, #00cf78 0%, #00a862 100%);
      transform: translateY(-1px);
      box-shadow: 0 4px 12px rgba(0, 185, 107, 0.3);
    }
  }
}
